[role="tablist"] {
  margin: 0 0 -0.1em;
  overflow: visible;
}

[role="tab"] {
  position: relative;
  margin: 0;
  padding: 0.7em 1.4em;
  border: 1px solid transparent;
  overflow: visible;
  color: #666;
  font-family: "proximanova", "HelveticaNeue", "Helvetica", "Arial", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  background: #f7f7f7;
}

[role="tab"]:hover {
  color: #444446;
  background: #eaeaea;
}

[role="tab"]:hover,
[role="tab"]:focus,
[role="tab"]:active {
  outline: 0;
}

[role="tab"][aria-selected="true"]::before {
  position: absolute;
  bottom: 100%;
  right: -1px;
  left: -1px;
  border-top: 3px solid #666;
  content: '';
}

[role="tab"][aria-selected="true"] {
  border: 1px solid #ccc;
  color: #444446;
  background: #fff;
}

[role="tab"][aria-selected="true"]:not(:hover)::before {
  border-top: 3px solid #666;
}

[role="tab"][aria-selected="true"]:hover::before {
  border-top: 3px solid #666;
}

[role="tab"]:hover::before,
[role="tab"]:focus::before {
  border-color: #999;
}

[role="tab"][aria-selected="true"]::after {
  position: absolute;
  z-index: 3;
  bottom: -1px;
  right: 0;
  left: 0;
  height: 0.3em;
  background: #fff;
  content: '';
}

[role="tabpanel"] {
  position: relative;
  z-index: 2;
  padding: 1.15em 1em 1em;
  border: 1px solid #ccc;
  color: #303033;
  background: #fff;
  font-size: 1rem;
}

[role="tabpanel"]:focus {
  border-color: #1c7c89;
  box-shadow: 0 0 0.1em #1c7c89;
  outline: 0;
}

[role="tabpanel"]:focus::after {
  position: absolute;
  bottom: 0;
  right: -1px;
  left: -1px;
  content: '';
}

[role="tabpanel"] p {
  margin: 0;
}

[role="tabpanel"] * + p {
  margin-top: 1em;
}

