$breadcrumb-active: $gray-darkest;

.breadcrumb {
  @include list-reset;

  margin-top: 1em;
}

.breadcrumb__item,
.breadcrumb__link,
.breadcrumb__link:link,
.breadcrumb__link:visited,
.breadcrumb__link:focus {
  color: $gray;
  text-decoration: none;
}

.breadcrumb__link:hover {
  text-decoration: underline;
}

.breadcrumb__item {
  display: inline-block;
  font-size: 0.875rem;

  &::after {
    content: "|";
    color: $red;
    font-weight: 700;
    margin: 0 0.1625em;
  }

  &:last-child {
    color: $breadcrumb-active;

    &::after {
      content: none;
    }
  }
}

.breadcrumb_link:active {
  color: $breadcrumb-active;
}
